<template>
  <div>
    <div>
      <router-view></router-view>
    </div>
    <!--  -->
    <div id="nav">
      <mt-tabbar v-model="selected">
        <mt-tab-item id="/">
          <i class="iconfont icon-xinxi1"></i>
          <div></div>
        </mt-tab-item>
        <mt-tab-item id="/no1_2">
          <i class="iconfont icon-tongxunlu1"></i>
          <div></div>
        </mt-tab-item>
        <mt-tab-item id="/no1_3">
          <i class="iconfont icon-faxian"></i>
          <div></div>
        </mt-tab-item>
        <mt-tab-item id="/no1_4">
          <i class="iconfont icon-wo"></i>
          <div></div>
        </mt-tab-item>
      </mt-tabbar>
    </div>
  </div>
</template>
<style scoped>
#nav i {
  font-size: 24px;
}
#nav div {
  font-size: 14px;
  padding-top: 6px;
}
.mint-tabbar > .mint-tab-item.is-selected{
    color:rgb(26, 180, 26);
    /* background:rgb(26, 180, 26); */
}
#alls{
    padding:4px 10px;
}
</style>
<script>
export default {
  data() {
    return {
      selected: ""
    };
  },
  watch: {
    selected() {
      location.href = "#" + this.selected;
    }
  },
  created() {
    this.selected = this.$route.path;
  }
};
</script>


