import Vue from "vue"
import Router from "vue-router"
import no1 from "./router/no1"
import no2 from "./router/no2"
import no1_1 from "./router/no1_1"
import no1_2 from "./router/no1_2"
import no1_3 from "./router/no1_3"
import no1_4 from "./router/no1_4"

Vue.use(Router)

var router = new Router({
    routes:[
        {
            path: '/', name: 'no1', component: no1,
            children: [
                { path: '/', name: 'no1_1', component: no1_1 },
                { path: 'no1_2', name: 'no1_2', component: no1_2 },
                { path: 'no1_3', name: 'no1_3', component: no1_3 },
                { path: 'no1_4', name: 'no1_4', component: no1_4 },
            ]
        },
        { path: '/no2', name: 'no2', component: no2 }
    ]
})
export default router


